body {
  margin: 0;
  font-family: 'Gluten','Oswald', 'Genos', 'Hachimaru', 'Moolahlah', 'Nerkoone', 'Readex', 'Rubikbeastly', 'Tradewinds', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
/* The element which contains one/many content sections */
.container {
  display: flex;
  flex-direction: column;
}

/* The container's content you want to add an overflow to */
.container .scrollable-content {
  flex-grow: 1;  
  overflow: auto;
}

/* Styling the scrollbar */
::-webkit-scrollbar {
  width: 10px;
  background: transparent;
}

/* Styling the track of the scrollbar */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 5px;
}

/* Styling the thumb (the draggable scrolling handle) */
::-webkit-scrollbar-thumb {
  background: var(--primary-text-color);
  border-radius: 10px; 
}

/* The thumb on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--primary-text-white-color);
  border: 1px solid grey;
}
