}
.navbar {
  position: absolute;
  width: 100%;
  height: 60px;
  top: 0px;
  background: #28293d;
  backdrop-filter: blur(80px);
  border-bottom: 2px solid #f1b90c;
}

.nav-btn {
  background: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}
.nav-icon,
.social-link {
  font-size: 1rem;
  color: var(--primary-text-color);
  margin: 0 0 0 2rem;
}
.social-links {
  padding: 5px 5px;
}
.social-links svg {
  margin: 6px;
}
.social-links2 {
  padding: 5px 3px;
  text-align: center;
}
.social-links2 svg {
  margin: 5px;
}
@media (max-width: 425px) {
  .social-links {
    padding: 5px 3px;
    display: none;
  }
}
@media (min-width: 426px) {
  .social-links2 {
    padding: 5px 3px;
    display: none;
  }
}

.social-link {
  font-size: 1.7rem;
  color: var(--offWhite);
  margin: 2px 10px;
  padding: 6px;
  background-color: rgba(255, 255, 255, 0.1);
  border-radius: 50%;
}
.nav-links,
.nav-mobile {
  height: 0;
  overflow: hidden;
  transition: var(--mainTransition);
  list-style-type: none;
  margin: 0 0;
}
.nav-mobile {
  height: auto;
  display: flex;
  margin: 0;
  float: right;
}
.nav-links a {
  display: block;
  text-decoration: none;
  color: var(--mainWhite);
  transition: var(--mainTransition);
  font-size: 0.8rem;
  border: 1px solid var(--primary-text-color);
  border-radius: 5px;
}
.connect-button {
  color: var(--mainWhite);
  transition: var(--mainTransition);
  font-size: 1rem;
  border: 1px solid #ffffff;
  border-radius: 5px;
  margin-left: 0.5rem;
  cursor: pointer;
  align-items: center;
  align-self: center;
  padding: 3px 10px;
  background-color: orange;
}
.connect-button:hover {
  background: transparent;
  color: var(--mainWhite);
  border-color: var(--mainWhite);
}
.navbar a {
  text-decoration: none;
}
.navbar a > span {
  color: var(--primary-text-color);
  font-weight: 700;
  font-size: 20px;
  vertical-align: middle;
}
.nav-links a {
  color: var(--primary-text-color);
}
.nav-links a:hover {
  color: var(--offWhite);
  border-color: var(--offWhite);
}
.show-nav {
  height: 220px;
}
@media screen and (min-width: 768px) {
  .navbar {
    padding: 0 0.5rem;
    justify-content: space-around;
  }
  .nav-btn {
    display: none;
  }
  .nav-center {
    max-width: 1170px;
  }
  .nav-mobile {
    display: none;
  }
  .nav-links {
    height: auto;
    display: flex;
    margin-left: 3rem;
  }
  .nav-links a {
    margin: 0.5rem 0.5rem;
    padding: 0.3rem 0.3rem;
  }
}
