.modal {
  --spacing: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  transform: all ease-in-out 0.9s;
  transition: all 1s linear;
  color: var(--primary-text-color);
}

.modal__close {
  display: block;
  position: fixed;
  background: rgba(15, 15, 15, 0.5);
  border: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  transition: all ease-in-out 0.9s;
}

.modal__inner {
  position: relative;
  width: calc(var(--spacing) * 36);
  max-width: 100%;
  /* padding: calc(var(--spacing) * 1.5) calc(var(--spacing) * 1.5); */
  border-radius: 20px;
  /* border: 1px solid rgba(248, 210, 86, 0.3); */
  background-color: var(--modal-background-color);
  box-shadow: rgba(19, 50, 66, 0.25) 0px 20px 32px -8px;
}
.modal__header {
  display: flex;
  flex-shrink: 0;
  align-items: center;
  justify-content: space-between;
  padding: 1rem 1rem;
  background: var(--modaltitle-background-color);
  /* border-bottom: 1px solid #dee2e6; */
  border-top-left-radius: calc(0.3rem + 20px);
  border-top-right-radius: calc(0.3rem + 20px);
}

.modal__body {
  position: relative;
  width: calc(var(--spacing) * 36);
  max-width: 100%;
  padding: calc(var(--spacing) * 1.5) calc(var(--spacing) * 3);
  border-radius: 20px;
  background-color: var(--modal-background-color);
  box-shadow: rgba(19, 50, 66, 0.25) 0px 20px 32px -8px;
}

.modal__close-btn {
  box-sizing: content-box;
  width: 2em;
  height: 2em;
  padding: 0.25em 0.25em;
  color: #ffa600;
  background: transparent;
  border: 0;
  border-radius: 0.25rem;
}

.modal__close-btn svg {
  width: var(--spacing);
  height: var(--spacing);
  fill: currentColor;
  opacity: 0.5;
}

.modal__close-btn:hover svg {
  opacity: 1;
}
/* ----------- End of useModal */

